import React, { Component } from 'react';
import { isAndroidClient } from '../../platform';

let widowsObj = {
    pageHeight: 0,
    viewportHeight: 0,
    scrollHeight: 0,
    startPos: {},
    movePos: {
        x: 0,
        y: 0
    },
    isScrolling: 0, //isScrolling10
    touchMoved: false, //touchmove,click
    touchMoveScroll: 0,
    touchEndScroll: 0,
}

export default class Pagination extends Component{
    static defaultProps = {
        currentPage:1,//
        onPageUp:() =>{refresh:false},//
        onPageDown:() =>{},//
        stopPageUp:false,//
        stopPageDown:false,//
    };

    componentDidMount(){
        widowsObj = {
            pageHeight: 0,
            viewportHeight: 0,
            scrollHeight: 0,
            startPos: {},
            movePos: {
                x: 0,
                y: 0
            },
            isScrolling: 0, //isScrolling10
            touchMoved: false, //touchmove,click
            touchMoveScroll: 0,
            touchEndScroll: 0,
            isRefresh: false, // 
        }   
        widowsObj.viewportHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0; //;  
    }
    componentWillUnmount() {
        //
        widowsObj = null;
    }

    handleTouchStart(event){
        var _touch = event.targetTouches[0]; //touchestouchtouch
        widowsObj.startPos = {
            x: _touch.pageX,
            y: _touch.pageY,
        }; //touch
        widowsObj.movePos = {
            x: 0,
            y: 0
        };
    }

    handleTouchMove = (event) =>{
        const { onPageUp, stopPageUp } = this.props;
        var _touch = event.targetTouches[0];
        widowsObj.movePos = {
            x: _touch.pageX - widowsObj.startPos.x,
            y: _touch.pageY - widowsObj.startPos.y
        };
        widowsObj.touchMoved = true; //touchmove
        widowsObj.isScrolling = Math.abs(widowsObj.movePos.x) < Math.abs(widowsObj.movePos.y) ? 1 : 0; //isScrolling10
        if (widowsObj.movePos.y >= 0 && !stopPageUp && widowsObj.isScrolling == 1) { //
            widowsObj.touchMoveScroll = window.pageYOffset || document.body.scrollTop;
            if (widowsObj.touchMoveScroll <= 0 && widowsObj.movePos.y >= 60) { //IOSscroll<0
                onPageUp({refresh:true});
                widowsObj.isRefresh = true;
            }
        }
    }
    
    handleScroll = () =>{ //tuchEnd
        const { currentPage, onPageUp, stopPageUp, onPageDown, stopPageDown } = this.props;
        if ((widowsObj.movePos.x != 0 || widowsObj.movePos.y != 0) && widowsObj.touchMoved) { //click:clicktouchmove
            if (widowsObj.movePos.y > 0 && !stopPageUp) { //
                widowsObj.touchEndScroll = window.pageYOffset || document.body.scrollTop;
                if (widowsObj.isRefresh) {
                    onPageUp({refresh:false});
                }
            } else {
                var _currentPage = currentPage;
                if (currentPage >= 1 && !stopPageDown) { // 
                    widowsObj.pageHeight = Math.max(document.body.scrollHeight, document.body.offsetHeight); //,

                    //=s
                    if(isAndroidClient() && widowsObj.viewportHeight < widowsObj.pageHeight  && _currentPage === 1){
                        widowsObj.viewportHeight = widowsObj.pageHeight;
                    }
                    //=e

                    widowsObj.scrollHeight = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop || 0; //
                    if (widowsObj.pageHeight - widowsObj.viewportHeight - widowsObj.scrollHeight < 60) { //
                        _currentPage++;
                        onPageDown({
                            currentPage:_currentPage,
                        });
                    }
                }
            }
            widowsObj.touchMoved = false;
        }
    }

    render() {
        const { children, className } = this.props;
        return (
            <div 
                className = {className}
                onTouchStart = {this.handleTouchStart} 
                onTouchMove = {this.handleTouchMove} 
                onTouchEnd = {this.handleScroll}
                                
            >
                {children}
            </div>
        )
    }
}