import React, { Component } from 'react';
import Loading from '../loading/Loading';
import MessageTip from '../messageTip/MessageTip';
import LoadListSuccess from './LoadListSuccess';
import LoadListError from './LoadListError';


export default class LoadList extends Component{
    // static propTypes = {
    //     list: React.PropTypes.array.isRequired
    // };
    
    static defaultProps = { //
        listClassName: 'datalist-list', //
        baselineClassName: 'datalist-baseline', //
        noDataClassName: 'data-isNull', //
        dataIsNull:null,
        noDataImgUrl: require('../../images/data_isNull.png'), //
        noDataMsg: '', //
        list: [],
        currentPage: 1,
        stopFlip: false,
        messageTip: '',
        itemKeyName:'',//itemkeykey
        isRefresh:false, //
        onPageUp:() =>{},//
        onPageDown:() =>{},//
    };
    
    componentDidMount() { 
        this.scrollFlip(1);
    }
      
    scrollFlip = (pageNo) =>{
        this.props.onPageDown(pageNo);
    }    
    
    render() {
        const { list, currentPage, isLoading, stopFlip, endPage } = this.props;
        if(isLoading && currentPage === 1){
            return (
                <Loading 
                    content = ''
                    classPrefix = "init"
                />
            )
        }else if(list != null && list.length > 0){
            return (
                <LoadListSuccess 
                    {...this.props}
                />
            );
        }else{
            return (
                <LoadListError
                    {...this.props}
                />
            )
        } 
    }
}


